












































export default {
	data() {
		return {
			type:0,
			malllist:[
			],
			guigetype:[
				{name:'803固态'},
				{name:'703固态'},
				{name:'603固态'},
				{name:'903固态'},
				{name:'803固态'},
				{name:'803固态'},
			],
			mallnum: 0,
			mallinf: {img:'../../static/build/mall.png',price:22.11,name:'不锈钢加厚荷叶',num:'300',mallnum:0},
			mallprice:0,
			barlist:[],
			pop:false
		}
	},
	onLoad() {},
	methods: {
		cg_type(type){
			this.type=type;
		},
		cg_pop(){
			if(!this.pop&&!this.mallnum){
				return;
			}
			this.pop=!this.pop;
		},
		go_build_pay(){
			wx.navigateTo({
				url: '../build/pay'
			})
		},
		minus(index){
			if(this.mallnum==0)return;
			this.mallnum-=1;
			this.mallinf.mallnum-=1;
			this.mallprice=(Number(this.mallprice)-Number(this.mallinf.price)).toFixed(2);
			this.barlist[index||0]=this.mallinf;
			if(this.mallinf.mallnum==0){
				this.barlist.splice(index||0,1);
				this.pop=false;
			}
		},
		add(index){
			this.mallnum+=1;
			this.mallinf.mallnum+=1;
			this.mallprice=(Number(this.mallprice)+Number(this.mallinf.price)).toFixed(2);
			this.barlist[index||0]=this.mallinf;
		},
		cleanbar(){
			this.mallnum=0;
			this.mallinf.mallnum=0;
			this.mallprice=0;
			this.barlist[0]=[];
			this.pop=false;
			
		}
	}
}



